"use client";

import { Card, CardContent, CardHeader, CardTitle } from "@/app/components/ui/card";
import { Button } from "@/app/components/ui/button";
import { RefreshCw, TrendingDown, TrendingUp, User, ChevronDown, ChevronUp, DollarSign, CheckCircle2 } from "lucide-react";
import { useState } from "react";
import { cn } from "@/lib/utils";
import type { MyBalanceSummaryDto, PersonalBalanceDto, ExpenseBreakdown } from "../types";
import SettleBalanceDialog from "./SettleBalanceDialog";

interface Props {
    myBalance: MyBalanceSummaryDto | null;
    loading: boolean;
    onRefresh: () => Promise<void>;
    tripId: string; // ← DODAJ
}

function PersonBalanceItem({ person, onSettleExpense, tripId }: { 
    person: PersonalBalanceDto; 
    onSettleExpense: (expense: ExpenseBreakdown, personName: string) => void; // ← DODAJ personName
    tripId: string;
}) {
    const [expanded, setExpanded] = useState(false);
    const isOwed = person.balance > 0;
    const iOwe = person.balance < 0;

    return (
        <div className={cn(
            "border rounded-lg overflow-hidden",
            isOwed && "border-green-200 dark:border-green-800",
            iOwe && "border-destructive/20"
        )}>
            {/* Header - clickable */}
            <button
                onClick={() => setExpanded(!expanded)}
                className={cn(
                    "w-full flex items-center justify-between p-3 hover:bg-muted/50 transition-colors",
                    isOwed && "bg-green-50 dark:bg-green-950/20",
                    iOwe && "bg-destructive/5"
                )}
            >
                <div className="flex items-center gap-3">
                    <div className={cn(
                        "w-8 h-8 rounded-full flex items-center justify-center text-xs font-bold",
                        isOwed && "bg-green-100 dark:bg-green-900 text-green-700 dark:text-green-300",
                        iOwe && "bg-destructive/10 text-destructive"
                    )}>
                        {person.userName.split(' ').map(n => n[0]).join('')}
                    </div>
                    <div className="text-left">
                        <p className="font-medium text-sm">{person.userName}</p>
                        <p className="text-xs text-muted-foreground">
                            {person.expenses.length} {person.expenses.length === 1 ? 'wydatek' : 'wydatki'}
                        </p>
                    </div>
                </div>

                <div className="flex items-center gap-2">
                    <span className={cn(
                        "font-bold text-sm",
                        isOwed && "text-green-600 dark:text-green-400",
                        iOwe && "text-destructive"
                    )}>
                        {isOwed ? '+' : ''}{person.balance.toFixed(2)} PLN
                    </span>
                    {expanded ? <ChevronUp className="w-4 h-4" /> : <ChevronDown className="w-4 h-4" />}
                </div>
            </button>

            {/* Expanded - expense breakdown */}
            {expanded && (
                <div className="border-t p-3 bg-muted/20 space-y-2">
                    {person.expenses.map((exp) => {
                        const expenseIsOwed = exp.balance > 0;
                        const expenseIOwe = exp.balance < 0;

                        return (
                            <div key={exp.expenseId} className={cn(
                                "p-3 bg-background rounded-lg border-2 space-y-2",
                                expenseIsOwed && "border-green-200 dark:border-green-800",
                                expenseIOwe && "border-destructive/20"
                            )}>
                                {/* Expense Header */}
                                <div className="flex justify-between items-start">
                                    <span className="font-medium text-sm">{exp.expenseTitle}</span>
                                    <span className={cn(
                                        "font-bold text-sm",
                                        expenseIsOwed && "text-green-600 dark:text-green-400",
                                        expenseIOwe && "text-destructive"
                                    )}>
                                        {expenseIsOwed ? '+' : ''}{exp.balance.toFixed(2)} PLN
                                    </span>
                                </div>

                                {/* Expense Details */}
                                <div className="text-xs text-muted-foreground space-y-0.5">
                                    <div className="flex justify-between">
                                        <span>Mój udział:</span>
                                        <span>{exp.myShare.toFixed(2)} PLN</span>
                                    </div>
                                    <div className="flex justify-between">
                                        <span>Ja zapłaciłem:</span>
                                        <span className="font-medium">{exp.iPaid.toFixed(2)} PLN</span>
                                    </div>
                                </div>

                                {/* Status Info */}
                                {expenseIsOwed && (
                                    <div className="text-xs text-green-600 dark:text-green-400 pt-2 border-t">
                                        → {person.userName} mi jest winien/winna {exp.balance.toFixed(2)} PLN
                                    </div>
                                )}
                                {expenseIOwe && (
                                    <div className="text-xs text-destructive pt-2 border-t">
                                        → Jestem winien/winna {person.userName} {Math.abs(exp.balance).toFixed(2)} PLN
                                    </div>
                                )}

                                {/* Settle Button per Expense */}
                                <Button
                                    onClick={(e) => {
                                        e.stopPropagation();
                                        onSettleExpense(exp, person.userName); // ← PRZEKAŻ person.userName
                                    }}
                                    variant={expenseIOwe ? "destructive" : "default"}
                                    size="sm"
                                    className="w-full"
                                >
                                    <CheckCircle2 className="w-3 h-3 mr-2" />
                                    {expenseIOwe 
                                        ? `Oznacz jako oddane` 
                                        : `Oznacz jako otrzymane`
                                    }
                                </Button>
                            </div>
                        );
                    })}
                </div>
            )}
        </div>
    );
}

function BalanceDonut({ netBalance, totalIOweThem, totalTheyOweMe }: { 
    netBalance: number; 
    totalIOweThem: number; 
    totalTheyOweMe: number; 
}) {
    const total = totalIOweThem + totalTheyOweMe;
    const iOwePercentage = total > 0 ? (totalIOweThem / total) * 100 : 0;
    const theyOwePercentage = total > 0 ? (totalTheyOweMe / total) * 100 : 0;

    const size = 180;
    const strokeWidth = 20;
    const radius = (size - strokeWidth) / 2;
    const circumference = 2 * Math.PI * radius;

    const iOweStrokeDash = (iOwePercentage / 100) * circumference;
    const theyOweStrokeDash = (theyOwePercentage / 100) * circumference;

    return (
        <div className="flex flex-col items-center">
            {/* SVG with centered text */}
            <div className="relative" style={{ width: size, height: size }}>
                <svg width={size} height={size} className="transform -rotate-90">
                    <circle
                        cx={size / 2}
                        cy={size / 2}
                        r={radius}
                        fill="none"
                        stroke="currentColor"
                        strokeWidth={strokeWidth}
                        className="text-muted/20"
                    />

                    {theyOwePercentage > 0 && (
                        <circle
                            cx={size / 2}
                            cy={size / 2}
                            r={radius}
                            fill="none"
                            stroke="currentColor"
                            strokeWidth={strokeWidth}
                            strokeDasharray={`${theyOweStrokeDash} ${circumference}`}
                            strokeLinecap="round"
                            className="text-green-500 transition-all duration-500"
                        />
                    )}

                    {iOwePercentage > 0 && (
                        <circle
                            cx={size / 2}
                            cy={size / 2}
                            r={radius}
                            fill="none"
                            stroke="currentColor"
                            strokeWidth={strokeWidth}
                            strokeDasharray={`${iOweStrokeDash} ${circumference}`}
                            strokeDashoffset={-theyOweStrokeDash}
                            strokeLinecap="round"
                            className="text-destructive transition-all duration-500"
                        />
                    )}
                </svg>

                {/* Center text - positioned absolutely in the middle */}
                <div className="absolute inset-0 flex flex-col items-center justify-center">
                    <p className="text-xs text-muted-foreground mb-1">Bilans</p>
                    <p className={cn(
                        "text-2xl font-bold",
                        netBalance > 0 && "text-green-600 dark:text-green-400",
                        netBalance < 0 && "text-destructive",
                        netBalance === 0 && "text-foreground"
                    )}>
                        {netBalance > 0 ? '+' : ''}{netBalance.toFixed(2)}
                    </p>
                    <p className="text-xs text-muted-foreground">PLN</p>
                </div>
            </div>

            {/* Legend */}
            <div className="mt-6 flex gap-6 text-xs">
                <div className="flex items-center gap-2">
                    <div className="w-3 h-3 rounded-full bg-green-500" />
                    <div>
                        <p className="text-muted-foreground">Mi są winni</p>
                        <p className="font-bold text-green-600 dark:text-green-400">
                            {totalTheyOweMe.toFixed(2)} PLN
                        </p>
                    </div>
                </div>
                <div className="flex items-center gap-2">
                    <div className="w-3 h-3 rounded-full bg-destructive" />
                    <div>
                        <p className="text-muted-foreground">Jestem winien/winna</p>
                        <p className="font-bold text-destructive">
                            {totalIOweThem.toFixed(2)} PLN
                        </p>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default function PersonalBalance({ myBalance, loading, onRefresh, tripId }: Props) {
    const [settleDialogOpen, setSettleDialogOpen] = useState(false);
    const [selectedExpense, setSelectedExpense] = useState<ExpenseBreakdown & { 
        personName: string;
        personUserId: string;
        tripId: string;
    } | null>(null);

    if (loading) {
        return (
            <Card className="sticky top-24">
                <CardHeader>
                    <CardTitle>Moje saldo</CardTitle>
                </CardHeader>
                <CardContent>
                    <div className="space-y-3">
                        <div className="h-48 bg-muted rounded-lg animate-pulse" />
                        {[1, 2].map(n => (
                            <div key={n} className="h-20 bg-muted rounded-lg animate-pulse" />
                        ))}
                    </div>
                </CardContent>
            </Card>
        );
    }

    if (!myBalance) {
        return (
            <Card className="sticky top-24">
                <CardHeader>
                    <CardTitle>Moje saldo</CardTitle>
                </CardHeader>
                <CardContent>
                    <p className="text-sm text-muted-foreground text-center py-8">
                        Brak danych do wyświetlenia
                    </p>
                </CardContent>
            </Card>
        );
    }

    const iOwe = myBalance.balances.filter(b => b.balance < 0);
    const theyOweMe = myBalance.balances.filter(b => b.balance > 0);
    const netBalance = myBalance.totalTheyOweMe - myBalance.totalIOweThem;
    const isSettled = netBalance === 0;

    const handleSettleExpense = (expense: ExpenseBreakdown, personName: string, personUserId: string, tripId: string) => {
        setSelectedExpense({ ...expense, personName, personUserId, tripId });
        setSettleDialogOpen(true);
    };

    return (
        <>
            <Card className="sticky top-24">
                <CardHeader className="flex flex-row items-center justify-between">
                    <CardTitle className="flex items-center gap-2">
                        <User className="w-5 h-5" />
                        Moje saldo
                    </CardTitle>
                    <Button variant="ghost" size="sm" onClick={onRefresh}>
                        <RefreshCw className="w-4 h-4" />
                    </Button>
                </CardHeader>
                <CardContent className="space-y-6">
                    
                    {!isSettled ? (
                        <BalanceDonut 
                            netBalance={netBalance}
                            totalIOweThem={myBalance.totalIOweThem}
                            totalTheyOweMe={myBalance.totalTheyOweMe}
                        />
                    ) : (
                        <div className="py-12 text-center">
                            <div className="w-16 h-16 rounded-full bg-green-100 dark:bg-green-900 flex items-center justify-center mx-auto mb-4">
                                <DollarSign className="w-8 h-8 text-green-600 dark:text-green-400" />
                            </div>
                            <p className="text-lg font-bold text-green-600 dark:text-green-400">
                                ✅ Wszystko rozliczone!
                            </p>
                            <p className="text-sm text-muted-foreground mt-1">
                                Nie masz żadnych zaległych płatności
                            </p>
                        </div>
                    )}

                    {/* People I Owe */}
                    {iOwe.length > 0 && (
                        <div>
                            <h4 className="text-sm font-semibold mb-3 flex items-center gap-2 text-destructive">
                                <TrendingDown className="w-4 h-4" />
                                Komu jestem winien/winna ({iOwe.length})
                            </h4>
                            <div className="space-y-2">
                                {iOwe.map((person) => (
                                    <PersonBalanceItem 
                                        key={person.userId} 
                                        person={person}
                                        tripId={tripId} // ← DODAJ
                                        onSettleExpense={(exp, name) => handleSettleExpense(exp, name, person.userId, tripId)}
                                    />
                                ))}
                            </div>
                        </div>
                    )}

                    {/* People Who Owe Me */}
                    {theyOweMe.length > 0 && (
                        <div>
                            <h4 className="text-sm font-semibold mb-3 flex items-center gap-2 text-green-600 dark:text-green-400">
                                <TrendingUp className="w-4 h-4" />
                                Kto mi jest winien ({theyOweMe.length})
                            </h4>
                            <div className="space-y-2">
                                {theyOweMe.map((person) => (
                                    <PersonBalanceItem 
                                        key={person.userId} 
                                        person={person}
                                        tripId={tripId} // ← DODAJ
                                        onSettleExpense={(exp, name) => handleSettleExpense(exp, name, person.userId, tripId)}
                                    />
                                ))}
                            </div>
                        </div>
                    )}

                </CardContent>
            </Card>

            {/* Settle Balance Dialog */}
            {selectedExpense && (
                <SettleBalanceDialog
                    open={settleDialogOpen}
                    onOpenChange={setSettleDialogOpen}
                    expense={selectedExpense}
                    myUserId={myBalance.myUserId}
                    onSettled={onRefresh}
                />
            )}
        </>
    );
}